*{
  padding:0px;
  margin:0px;
  box-sizing: border-box;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif, sans-serif;

}


.header{
  text-align: center;
  padding-top:40px;
  background-color: white;
  color:#495464;
 
  
}
.header h1,.home_h1{
  font-weight: 100;
  font-size: 52px;
  letter-spacing: 3px;
  margin-bottom: 20px;
}
.header h3,.childprod p{
  font-weight: 100;
  font-size: 20px;
  margin-bottom: 20px;
}
.navbar{
  background-color:#e8e8e895 ;
  color:#495464;
  padding:30px;
  text-align: center;
  font-weight: 400;
  font-size: 18px;
  margin-bottom: 20px;
  width:100%;
}

.row{
  display: flex;
  align-items: center; 
  margin:10px 0px;
  height: 100%;
}
.btn{
  color:#e8e8e8fd;
  border: none;
  padding:10px;
  border-radius: 10px;
  background-color: #495464;
  margin:10px 0px;
  
}
.btn1{
  color:#000000fd;
  border: none;
  padding:10px;
  border-radius: 10px;
  background-color: #4954647f;
  margin:10px 0px;
  
}
.row div{
  flex-basis: 50%;
  padding:20px;
}
.row img{
border-radius: 20px;
}
.card_container{
  
  display: flex;
  justify-content:space-around;
  flex-wrap:wrap;
  gap:10px 10px;
  
}
.card_inner{
  flex-basis: 30%;
 
}
.card_inner:hover{
  cursor: pointer;
}
.card_content{
  text-align: left;
  margin:10px 0px;
}
.card_content h3{
  text-align: left;
  margin:10px 0px;
  font-weight: 400;
}
.card_content h6{
  text-align: left;
  margin:10px 0px;
  font-size: 16px;
  font-weight: 400;
}
.card_inner div{
  width: 100%;
}
.card_inner div img{
  width: 100%;
  height: 60vh;
}
.subscribe_form{
  margin-top:20px;
  background-color: #495464;
  color: #e8e8e895;
  padding: 30px;
  text-align: center;
}
.subscribe_form input,.childprod input{
  padding:10px;
  margin:10px;
  border:1px solid #e8e8e895;
  color:#e8e8e895;
  border-radius: 10px;
}
.container_com{
  width: 70%;
  margin: 10px auto;
  background-color: #4954648f;
  color:#e8e8e895;
  display: flex;
  justify-content:space-around;
  align-items: center;
padding: 20px;
}
.container_prod{
  display: flex;
justify-content: space-around;
}
.childprod{
  flex-basis: 40%;
  height:70vh;
  text-align: left;

}
.childprod img{
  width:100%;
  height:100%;
}
.cart_prod{
 
  display: flex;
  justify-content:center;
  gap:10px;
  margin:20px 0px;
}
.cart_prod .childprod:nth-child(1){
  flex-basis: 20%;
  height: 30vh;
}
.cart_prod .childprod:nth-child(2){
  flex-basis: 40%;
  height: 30vh;
}
.cart_prod image{
  height:100%;
}
.cart_prod p{
  font-size:16px;
}
.cart_prod input{
padding:3px 1px;
width:40%;
margin:0px;
}

@media (max-width:780px){
  .header{
    text-align: center;
    padding: 0px 10px;
    padding-top:50px;
    background-color: white;
    color:#495464;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif, sans-serif;
    
  }
  .header h1{
    font-weight: 100;
    font-size: 30px;
    letter-spacing: 3px;
    margin-bottom: 20px;
  }
  .header h3{
    font-weight: 100;
    font-size: 16px;
    margin-bottom: 20px;
  }
  .navbar{
    background-color:#e8e8e895 ;
    color:#495464;
    padding:30px;
    text-align: center;
    font-weight: 400;
    font-size: 16px;
    margin-bottom: 20px;
    width: 100%;
  }
  .row{
    display: flex;
    flex-direction: column;
    align-items: center; 
    margin:10px 0px;
    height: 100%;
  }
  .row:nth-child(2)
  {
    flex-direction: column-reverse;
  }
 
  .card_inner{
    flex-basis: 100%;
   
  }
  .container_com{
    width: 70%;
    margin: 10px auto;
    color:#e8e8e895;
    display: flex;
    flex-direction: column-reverse;
    justify-content:space-around;
    align-items: center;
  padding: 20px;
  }
  .container_com div{
    margin:20px 0px;
  }
  .container_prod{
    display: flex;
 flex-direction: column;
  }
  .childprod{
    flex-basis: 100%;
    height:70vh;
    text-align: left;
  }
 
}